<template>
  <div class="videoplay">
    <div class="icon">
      <van-icon @click="$router.go(-1)" name="arrow-left" />
    </div>
    <div class="video">
      <video
        controls
        autoplay
        :src="urls"
      ></video>
    </div>
    <van-tabs class="content" v-model="active">
      <van-tab class="left" title="相关">
        <ul>
          <li v-for="item in allvideo" :key="item.vid">
            <div class="img" @click="play(item.vid)">
              <img
                :src="item.coverUrl"
                alt=""
              />
            </div>
            <div class="info">
              <h2>
                {{item.title}}
              </h2>
              <h3>{{item.creator[0].userName}}</h3>
            </div>
          </li>
        </ul>
      </van-tab>
      <van-tab class="ringt" title="评论">
        <ul>
          <li v-for="item in comments" :key="item.commentId">
            <div class="img">
              <img
                :src="item.user.avatarUrl"
                alt=""
              />
            </div>
            <div class="info">
              <h2>{{item.user.nickname}}</h2>
              <h3>{{item.timeStr}}</h3>
              <div>
                <p>{{item.content}}</p>
              </div>
            </div>
            <div class="icons">
              <span>{{item.likedCount}}</span>
              <van-icon name="good-job-o" />
            </div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { getvideourl,getallvideo ,getvideocomment} from "../api/base";
export default {
  data() {
    return {
      active: 0,
      urls:'',
      allvideo:"",
      comments:"",
      videoid:null
    };
  },
  created() {
     this.videoid=this.$route.params.id;
     this.fetchData()
  },
  methods: {
    fetchData() {
      getvideourl({ id: this.videoid }).then((data) => {
        this.urls=data.urls[0].url
      });

       getallvideo({id:this.videoid}).then(data=>{
        // console.log(data.data);
        this.allvideo=data.data
      })
      getvideocomment({id:this.videoid}).then(data=>{
        // console.log(data.comments);
        this.comments=data.comments
      })
    },
     play(id){
       this.videoid=id
       this.fetchData()
    },
  },
  
};
</script>

